import { Utility } from './helper/config'
import {Enviroment} from './helper/enums'
import {sayHello} from "./greet";

class MainPage{

    span: HTMLElement;

    constructor(){
        this.span = document.createElement('span');        
        this.span.innerText = Utility.MongoDBConnection(Enviroment.Development)    
    }


public hello(compiler:string){
    console.log(`Selam ${compiler}`);
}

public showHello(divName: string, name:string){
    const elt = document.getElementById(divName);    
    elt.appendChild(this.span)
    elt.innerText = sayHello(name);    
}

}

let page = new MainPage();
page.showHello("greeting",Utility.MongoDBConnection(Enviroment.Development));




